<template>
    <div>
        <v-btn :to="{ name: 'Message' }" class="hidden-md-and-up">Back</v-btn>
        <v-card
                class="pa-5"
                outlined
                tile
        >
            <b>orderID: {{ currentOrder.quotation.orderId }}
<!--                {{-->
<!--                auth._id === currentOrder.employerId._id ? currentOrder.freelancerId.username :-->
<!--                currentOrder.employerId.username-->
<!--                }} -->
            </b>
        </v-card>
        <v-card
                v-chat-scroll
                class="card pa-2"
                outlined
                tile
        >
            <div v-for="msg in chat.messages" :key="msg.index">
                <div v-if="msg.from !== auth._id"  class="msg-box receive">
                       <v-img width="200" v-if="msg.image" :src="msg.image ? $server + msg.image : ''"></v-img>
                    <div class="receive-msg">
                        <p v-if="msg.message">{{ msg.message }}</p>
                        <v-card v-if="msg.type === 'quotation'">
                              <div class="mx-5 my-5">
                                 <b> ໃບສະເໜີລາຄາ </b>
                                  <br>
                                  <small class="mb-5"> {{ currentOrder.portfolioId.title }}</small>
                                  <br>
                                  <b>ລາຄາ</b>
                                  <span class="primary--text"> {{ msg.orderId.quotation.price.toLocaleString() }}
                                  </span>
                                  <b>ກີບ</b>
                                  <app-detail-quotation-modal
                                          :auth="auth"
                                          :order="msg.orderId"
                                          :currentOrder="currentOrder"
                                  ></app-detail-quotation-modal>
                              </div>
                        </v-card>
                    </div>
                </div>
                <div v-else class="msg-box send">
                    <v-img width="200" v-if="msg.image" :src="msg.image ? $server + msg.image : ''"></v-img>
                    <div class="send-msg">
                        <p v-if="msg.message">{{ msg.message }}</p>
                        <v-card v-if="msg.type === 'quotation'">
                            <div class="mx-5 my-5">
                                <b> ໃບສະເໜີລາຄາ </b>
                                <br>
                                <small class="mb-5"> {{ currentOrder.portfolioId.title }}</small>
                                <br>
                                <b>ລາຄາ</b>
                                <span class="primary--text"> {{ msg.orderId.quotation.price.toLocaleString() }} </span>
                                <b>ກີບ</b>
                                <app-detail-quotation-modal
                                        :auth="auth"
                                        :order="msg.orderId"
                                        :currentOrder="currentOrder"
                                ></app-detail-quotation-modal>
                            </div>
                        </v-card>
                    </div>
                </div>
            </div>
            <div v-if="typing" class="msg-box receive">
                <div class="receive-msg">
                    <p>ກໍາລັງພິມ...</p>
                </div>
            </div>
            <v-card v-if="base64" outlined tile>
                <v-img
                        width="150"
                        height="150"
                        :src="base64"
                        class="mx-2 my-2"
                        style="border: 1px solid white">
                    <v-icon
                            @click="base64='', file=''"
                            size="30"
                            class="error--text mb-2"
                            style="cursor: pointer">
                        mdi-cancel</v-icon>
                </v-img>
            </v-card>
        </v-card>

        <v-card outlined tile style="height: 74px;">
            <v-form @submit.prevent="sendMessage">
                <v-layout>
                    <v-flex xs10 sm10 md10 lg10>
                        <v-text-field
                                v-model="input"
                                class="ml-4"
                                counter="20">
                        </v-text-field>
                    </v-flex>
                    <v-flex mt-5>
                        <a @click="sendMessage" class="icon mx-2">
                            <v-icon size="30">mdi-send</v-icon>
                        </a>
                        <a class="icon file-input">
                            <label for="file">
                                <v-icon size="30">mdi-link-variant </v-icon>
                            </label>
                            <input @change="imgUpload" type="file" id="file">
                        </a>
                    </v-flex>
                </v-layout>
            </v-form>
        </v-card>
    </div>
</template>

<script>
    import Vue from 'vue'
    import VueChatScroll from 'vue-chat-scroll'
    import { mapActions, mapGetters } from 'vuex'
    // Socket.io with ES6 import

    import appDetailQuotationModal from '../../components/message/modal/DetailQuotationModal'


    Vue.use(VueChatScroll)
    export default {
        components: {
            appDetailQuotationModal
        },
        data: () => ({
            base64: '',
            file: '',
            input: '',
            typing: false,
            chat: {
                messages: [],
            },
            from: '',
            to: ''
        }),
        computed: {
          ...mapGetters([
              'auth',
              'currentOrder'
          ]),
        },
        methods: {
            ...mapActions([
               'fetchCurrentOrder'
            ]),
            async sendMessage () {
                 try {
                     if(this.input.length <= 20 && this.input.length !== 0 || this.file) {
                         if(this.auth._id === this.currentOrder.employerId._id) {
                             this.from = this.currentOrder.employerId._id
                             this.to = this.currentOrder.freelancerId._id
                         }
                         if(this.auth._id === this.currentOrder.freelancerId._id) {
                             this.from = this.currentOrder.freelancerId._id
                             this.to = this.currentOrder.employerId._id
                         }
                         // formData
                         const formData = new FormData()
                         formData.append('message', this.input)
                         formData.append('from', this.from)
                         formData.append('to', this.to)
                         formData.append('orderId', this.currentOrder._id)
                         formData.append('image', this.file)
                         // sent message
                         const results = await this.$axios.post('message/send', formData)
                         const msg = results.data.messages
                         // push messages
                         this.chat.messages.push(msg)
                         // emit socket
                         this.$io.emit(`message`, msg)
                         this.input = ''
                         this.file = ''
                         this.base64 = ''
                         this.typing = false
                     }
                 } catch (e) {
                     throw new Error(e)
                 }
            },
            async fetchMessage () {
               const results = await this.$axios.get('message/user-message', { params: {
                       orderId: this.$route.params.id
                   }})
                this.chat.messages = results.data.messages
            },
            imgUpload(e) {
                const file = e.target.files[0]
                const reader = new FileReader() // Only read base64 not FormData
                if(file['size'] < 2000000) {
                    reader.onloadend = e => {
                        this.base64 = e.target.result
                    }
                    reader.readAsDataURL(file) // Base64 Reader
                    this.file = file // FormData Image
                }else {
                    this.$swal({
                        title: 'ຮູບພາບຂະໜາດໃຫຍ່ເກີນໄປ',
                        toast: true,
                        type: 'error',
                        position: 'top-end',
                        showConfirmButton: false,
                        timer: 2000
                    })
                }
            },
        },
        watch: { // Fetch new
            '$route.params': {
                handler(newValue) {
                    const { id } = newValue
                     this.fetchCurrentOrder(id)
                     this.fetchMessage(id)
                },
                immediate: true,
            }
        },
        mounted () {
            // socket io on message
            this.$io.on(`message`, (data) => {
                const { orderId } = data
                if(orderId._id === this.currentOrder._id) {
                    this.chat.messages.push(data)
                }
            })
            // socket io on update message
            this.$io.on('update-message', () => this.fetchMessage(this.currentOrder._id))
        }
    }
</script>

